
.location-list {
  position: fixed;
  top: 50px;
  left: 2%;
  right: 2%;
  bottom: 0;
  background-color: #fff;
  -webkit-box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2), -1px 0 1px rgba(0, 0, 0, 0.2);
          box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2), -1px 0 1px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}
.location-list .location-item {
    position: relative;
    padding: 0 20px;
    height: 60px;
    line-height: 60px;
    position: relative;
}
.location-list .location-item:after {
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      content: ' ';
      width: 100%;
      height: 0;
      border-bottom: 1px solid rgba(128, 128, 128, 0.1);
      -webkit-transform-origin: left 0;
              transform-origin: left 0;
}
@media (-webkit-m1in-device-pixel-ratio: 1.5), (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 1.5dppx) {
.location-list .location-item:after {
          width: 150%;
          -webkit-transform: scale(0.7);
          transform: scale(0.7);
}
}
@media (-webkit-m1in-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
.location-list .location-item:after {
          width: 200%;
          -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
}
@media (-webkit-m1in-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
.location-list .location-item:after {
          width: 300%;
          -webkit-transform: scale(0.333);
          transform: scale(0.333);
}
}
.location-list .location-item.active {
      background-color: rgba(230, 230, 230, 0.5);
}
.location-list .info {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background-color: #fff;
    z-index: 10;
}
.location-list .info .row {
      padding: 20px;
      height: 60px;
      line-height: 60px;
      position: relative;
}
.location-list .info .row:after {
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        content: ' ';
        width: 100%;
        height: 0;
        border-bottom: 1px solid rgba(128, 128, 128, 0.1);
        -webkit-transform-origin: left 0;
                transform-origin: left 0;
}
@media (-webkit-m1in-device-pixel-ratio: 1.5), (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 1.5dppx) {
.location-list .info .row:after {
            width: 150%;
            -webkit-transform: scale(0.7);
            transform: scale(0.7);
}
}
@media (-webkit-m1in-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
.location-list .info .row:after {
            width: 200%;
            -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
}
@media (-webkit-m1in-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
.location-list .info .row:after {
            width: 300%;
            -webkit-transform: scale(0.333);
            transform: scale(0.333);
}
}



