
.modal .modal-content .content-validation {
  font-size: 12px;
}
.modal .modal-content .content-validation .info {
    color: red;
}
.modal .modal-content .content-validation .text {
    color: gray;
}
.modal .modal-content .content-validation .count-wrapper {
    width: 60px;
    padding: 3px 5px;
    margin: 20px auto;
    border: 1px solid #080815;
    border-radius: 3px;
}
.modal .modal-content .content-validation .count-wrapper.disabled {
      border-color: rgba(8, 8, 31, 0.3);
}
.modal .modal-content .content-validation .count-wrapper .count {
      color: rgba(0, 0, 0, 0.5);
}
.modal .modal-content .content-validation .count-wrapper .text {
      color: black;
}
.modal .modal-content .content-validation .code-input {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    height: 52px;
}
.modal .modal-content .content-validation .code-input .input-wrapper {
      width: 50px;
      height: 50px;
      font-size: 30px;
}
.modal .modal-content .content-validation .code-input .input-wrapper + .input-wrapper {
        margin-left: 5px;
}
.modal .modal-content .content-validation .code-input .input-wrapper.active {
        outline: 1px solid #fc9153;
}
.modal .modal-content .content-validation .code-input .input-wrapper.filled {
        outline: 1px solid #fc9153;
}
.modal .modal-content .content-validation .code-input .input-wrapper input {
        width: 100%;
        text-align: center;
}



