
.modal .modal-content .log-content {
  padding: 0 15%;
  font-size: 16px;
}
.modal .modal-content .log-content input {
    width: 150px;
    text-align: center;
}
.modal .modal-content .log-content .content {
    padding: 3px 0;
    position: relative;
}
.modal .modal-content .log-content .content:after {
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      content: ' ';
      width: 100%;
      height: 0;
      border-bottom: 1px solid rgba(128, 128, 128, 0.1);
      -webkit-transform-origin: left 0;
              transform-origin: left 0;
}
@media (-webkit-m1in-device-pixel-ratio: 1.5), (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 1.5dppx) {
.modal .modal-content .log-content .content:after {
          width: 150%;
          -webkit-transform: scale(0.7);
          transform: scale(0.7);
}
}
@media (-webkit-m1in-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
.modal .modal-content .log-content .content:after {
          width: 200%;
          -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
}
@media (-webkit-m1in-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
.modal .modal-content .log-content .content:after {
          width: 300%;
          -webkit-transform: scale(0.333);
          transform: scale(0.333);
}
}
.modal .modal-content .log-content .content.user {
      margin-bottom: 10px;
}
.modal .modal-content .log-content .text {
    font-size: 10px;
}



