
.city-list {
  position: fixed;
  top: 50px;
  left: 2%;
  right: 2%;
  bottom: 0;
  -webkit-box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2), -1px 0 1px rgba(0, 0, 0, 0.2);
          box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2), -1px 0 1px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}
.city-list .cur-city {
    position: relative;
    padding: 0 20px;
    height: 40px;
    line-height: 40px;
    background-color: #fff;
    z-index: 20;
}
.city-list .city-flag-fixed {
    position: absolute;
    width: 100%;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    font-weight: 200;
    color: gray;
    background-color: #f7f7f7;
    z-index: 10;
}
.city-list .other-city {
    position: absolute;
    width: 100%;
    left: 0;
    top: 40px;
    bottom: 0;
    background-color: #fff;
    overflow: hidden;
}
.city-list .other-city .city-flag {
      padding: 0 20px;
      height: 40px;
      line-height: 40px;
      font-size: 14px;
      font-weight: 200;
      color: gray;
      background-color: #f7f7f7;
}
.city-list .other-city .city-item {
      position: relative;
      padding: 0 20px;
      height: 40px;
      line-height: 40px;
      position: relative;
}
.city-list .other-city .city-item:after {
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        content: ' ';
        width: 100%;
        height: 0;
        border-bottom: 1px solid rgba(128, 128, 128, 0.1);
        -webkit-transform-origin: left 0;
                transform-origin: left 0;
}
@media (-webkit-m1in-device-pixel-ratio: 1.5), (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 1.5dppx) {
.city-list .other-city .city-item:after {
            width: 150%;
            -webkit-transform: scale(0.7);
            transform: scale(0.7);
}
}
@media (-webkit-m1in-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
.city-list .other-city .city-item:after {
            width: 200%;
            -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
}
@media (-webkit-m1in-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
.city-list .other-city .city-item:after {
            width: 300%;
            -webkit-transform: scale(0.333);
            transform: scale(0.333);
}
}
.city-list .other-city .city-item.active {
        background-color: rgba(230, 230, 230, 0.5);
}
.city-list .info {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    background-color: #fff;
    z-index: 21;
}
.city-list .info .text {
      position: absolute;
      top: 50%;
      width: 100%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
}



