
.modal .phone-content {
  display: inline-block;
  padding: 0 15px;
  font-size: 14px;
  position: relative;
}
.modal .phone-content:after {
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    content: ' ';
    width: 100%;
    height: 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.8);
    -webkit-transform-origin: left 0;
            transform-origin: left 0;
}
@media (-webkit-m1in-device-pixel-ratio: 1.5), (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 1.5dppx) {
.modal .phone-content:after {
        width: 150%;
        -webkit-transform: scale(0.7);
        transform: scale(0.7);
}
}
@media (-webkit-m1in-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
.modal .phone-content:after {
        width: 200%;
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
}
}
@media (-webkit-m1in-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
.modal .phone-content:after {
        width: 300%;
        -webkit-transform: scale(0.333);
        transform: scale(0.333);
}
}
.modal .phone-content input {
    width: 100px;
    height: 100%;
}



