
.modal-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 50;
}
.modal-wrapper.non-bgc .modal {
    background-color: transparent;
}
.modal-wrapper .modal {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 250px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    background-color: #fff;
    z-index: 2;
}
.modal-wrapper .modal .modal-icon {
      margin: 20px 0 16px;
}
.modal-wrapper .modal .icon-close {
      position: absolute;
      padding: 5px;
      top: 0;
      right: 0px;
      color: gray;
}
.modal-wrapper .modal .icon-back {
      position: absolute;
      top: 5px;
      left: 5px;
      color: gray;
}
.modal-wrapper .modal .modal-title {
      font-size: 18px;
}
.modal-wrapper .modal .modal-title:first-child {
        margin-top: 30px;
}
.modal-wrapper .modal .modal-title:only-child {
        margin: 0;
}
.modal-wrapper .modal .modal-title + .modal-content {
        margin-top: 12px;
}
.modal-wrapper .modal .modal-content {
      margin: 16px 0;
      line-height: 22px;
      font-size: 0;
      overflow: hidden;
}
.modal-wrapper .modal .modal-btn-group {
      font-size: 0;
      overflow: hidden;
      position: relative;
}
.modal-wrapper .modal .modal-btn-group:before {
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 0;
        content: ' ';
        border-top: 1px solid rgba(128, 128, 128, 0.1);
        -webkit-transform-origin: left 0;
                transform-origin: left 0;
}
@media (-webkit-m1in-device-pixel-ratio: 1.5), (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 1.5dppx) {
.modal-wrapper .modal .modal-btn-group:before {
            width: 150%;
            -webkit-transform: scale(0.7);
            transform: scale(0.7);
}
}
@media (-webkit-m1in-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
.modal-wrapper .modal .modal-btn-group:before {
            width: 200%;
            -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
}
@media (-webkit-m1in-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
.modal-wrapper .modal .modal-btn-group:before {
            width: 300%;
            -webkit-transform: scale(0.333);
            transform: scale(0.333);
}
}
.modal-wrapper .modal .modal-btn-group.two-btn .modal-btn {
        float: left;
        width: 50%;
}
.modal-wrapper .modal .modal-btn-group .modal-btn {
        display: inline-block;
        width: 100%;
        padding: 17px 0;
        font-size: 16px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
}
.modal-wrapper .modal .modal-btn-group .modal-btn-cancel {
        position: relative;
}
.modal-wrapper .modal .modal-btn-group .modal-btn-cancel + .modal-btn-confirm {
          background-color: #fff;
}
.modal-wrapper .modal .modal-btn-group .modal-btn-cancel:after {
          display: block;
          position: absolute;
          right: 0;
          top: 0;
          height: 100%;
          content: ' ';
          border-right: 1px solid rgba(128, 128, 128, 0.1);
          -webkit-transform-origin: center 0;
                  transform-origin: center 0;
}
@media (-webkit-m1in-device-pixel-ratio: 1.5), (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 1.5dppx) {
.modal-wrapper .modal .modal-btn-group .modal-btn-cancel:after {
              -webkit-transform: scale(0.7);
              transform: scale(0.7);
}
}
@media (-webkit-m1in-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
.modal-wrapper .modal .modal-btn-group .modal-btn-cancel:after {
              -webkit-transform: scale(0.5);
              transform: scale(0.5);
}
}
@media (-webkit-m1in-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
.modal-wrapper .modal .modal-btn-group .modal-btn-cancel:after {
              height: 300%;
              -webkit-transform: scale(0.333);
              transform: scale(0.333);
}
}
.modal-wrapper .modal .modal-btn-group .modal-btn-confirm {
        color: #fff;
        background-color: rgba(58, 58, 58, 0.3);
}
.modal-wrapper .modal .modal-btn-group .modal-btn-confirm.active {
          background-color: #3a3a3a;
}
.modal-wrapper .modal .modal-btn-group .modal-btn-confirm .dot-wrapper {
          width: 100%;
          height: 100%;
}
.modal-wrapper .modal .modal-btn-group .modal-btn-confirm .dot-wrapper .dot {
            display: inline-block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: #fff;
}
.modal-wrapper .modal .modal-btn-group .modal-btn-confirm .dot-wrapper .dot + .dot {
              margin-left: 5px;
}
.modal-wrapper .modal .modal-btn-group .modal-btn-confirm .dot-wrapper .dot.dot-1 {
              -webkit-animation: scale .5s infinite linear;
                      animation: scale .5s infinite linear;
}
.modal-wrapper .modal .modal-btn-group .modal-btn-confirm .dot-wrapper .dot.dot-2 {
              -webkit-animation: scale .5s .1s infinite linear;
                      animation: scale .5s .1s infinite linear;
}
.modal-wrapper .modal .modal-btn-group .modal-btn-confirm .dot-wrapper .dot.dot-3 {
              -webkit-animation: scale .5s .2s infinite linear;
                      animation: scale .5s .2s infinite linear;
}
.modal-wrapper .modal-mask {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(230, 230, 230, 0.5);
}
@-webkit-keyframes scale {
0% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
20% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes scale {
0% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
20% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}



