
.list-nav {
  position: absolute;
  top: 50%;
  right: 10px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 20;
}
.list-nav .item {
    padding: 2px 10px;
    font-size: 14px;
    text-align: center;
}
.list-nav .letter {
    position: absolute;
    top: 50%;
    left: -50vw;
    margin-top: -25px;
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
    width: 50px;
    height: 50px;
    border-radius: 5px;
    text-align: center;
    line-height: 50px;
    color: #fff;
    background-color: #000;
    z-index: 10;
}



