
.list-wrapper {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 30;
}
.list-wrapper.slide-fade-enter, .list-wrapper.slide-fade-leave-to {
    -webkit-transform: translateY(70vh);
            transform: translateY(70vh);
    opacity: 0;
}
.list-wrapper.slide-fade-enter-active, .list-wrapper.slide-fade-leave-active {
    -webkit-transition: all .15s linear;
    transition: all .15s linear;
}
.list-wrapper .cur-city {
    padding-top: 3px;
    padding-right: 5px;
    font-size: 12px;
    position: relative;
}
.list-wrapper .cur-city:after {
      display: block;
      position: absolute;
      right: 0;
      top: 20%;
      bottom: 20%;
      content: ' ';
      border-right: 1px solid rgba(128, 128, 128, 0.1);
      -webkit-transform-origin: center 0;
              transform-origin: center 0;
}
@media (-webkit-m1in-device-pixel-ratio: 1.5), (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 1.5dppx) {
.list-wrapper .cur-city:after {
          -webkit-transform: scale(0.7);
          transform: scale(0.7);
}
}
@media (-webkit-m1in-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
.list-wrapper .cur-city:after {
          -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
}
@media (-webkit-m1in-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
.list-wrapper .cur-city:after {
          width: 300%;
          -webkit-transform: scale(0.333);
          transform: scale(0.333);
}
}
.list-wrapper .cur-city .icon {
      position: relative;
      display: inline-block;
      top: 3px;
      padding-right: 4px;
}
.list-wrapper .cur-city .text {
      display: inline-block;
      padding: 0 5px 0 15px;
      font-size: 12px;
      line-height: 36px;
}



