
.header-wrapper {
  position: relative;
  padding: 10px;
  -webkit-box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.1);
  z-index: 10;
}
.header-wrapper .header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    margin-bottom: 10px;
}
.header-wrapper .header .city {
      line-height: 30px;
}
.header-wrapper .header .city .text {
        display: inline-block;
        margin-right: 5px;
}
.header-wrapper .header .city .icon {
        position: relative;
        top: 2px;
}
.header-wrapper .tab {
    position: relative;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
}
.header-wrapper .tab ul {
      width: 387px;
      font-size: 0;
}
.header-wrapper .tab ul .type {
        display: inline-block;
        font-size: 16px;
        font-weight: 200;
        color: #000;
}
.header-wrapper .tab ul .type.current {
          color: orange;
}
.header-wrapper .tab ul .type:not(:last-child) {
          margin-right: 15px;
}
.header-wrapper .tab .all-type {
      position: absolute;
      top: 0;
      right: 0;
      height: 16px;
      -webkit-box-shadow: -5px 0 3px #fff;
              box-shadow: -5px 0 3px #fff;
      background-color: #fff;
}



